
.Btn-Wedgewood, .Btn-Flamenco, .Btn-Flamenco1{
    font-size: 14px;
    color: var(--White);
    padding: 8px 26px;
    border-radius: 10px;
    border: 1px solid var(--mainColor);
    transition: all .3s ease-out;
    white-space: nowrap;
    &.defLink{
        line-height: normal;
        display: inline-block;
    }
    &:hover{
        transition: all .3s ease-out;
    }
    &:focus{
        outline: none;
    }
}
.Btn{
    &-Flamenco{
        background-color: var(--Flamenco);
        border-color: var(--Flamenco);
        &:hover{
            background-color: transparent;
        }
    }
    &-Flamenco1{
        background-color: var(--White);
        border-color: var(--Flamenco);
        color: var(--Flamenco);
        &:hover{
            background-color: var(--Flamenco);
            color: var(--White);
        }
    }
    &-Wedgewood{
        background-color: transparent;
        border-color: var(--Wedgewood);
        color: var(--Wedgewood);
        &:hover{
            background-color: var(--Wedgewood);
            color: var(--White);
        }
    }
}
.Btn-Nobel{
    font-size: 14px;
    color: #a1a1a1;
}
.basketBtn{
    color: var(--White);
    background-color: var(--Wedgewood);
    border-radius: 10px;
    font-size: 23px;
    padding: 7px 17px 7px 15px;
    border: none;
    display: flex;
    cursor: pointer;
    transition: all .3s ease-out;
    &.active{
        background-color: var(--Flamenco);
        i::before{
            content: "\E034";
        }
        .popup-desc{
            display: block;
        }
    }
    .popup-desc{
        display: none;
        white-space: nowrap;
        top: -42px;
        left: 0px;
    }
    &:hover{
        background-color: var(--Flamenco);
        transition: all .3s ease-out;
    }
}